@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,600&display=swap);
#footer-container{display:grid;grid-template-columns:1fr 2fr;grid-column-gap:2rem;grid-row-gap:2rem}@media (max-width: 1205px){#footer-container{grid-template-columns:auto}}#footer-links{display:grid;grid-template-columns:repeat(4, auto);align-items:start;justify-items:center}@media (max-width: 773px){#footer-links{grid-template-columns:repeat(2, auto)}}@media (max-width: 419px){#footer-links{grid-template-columns:auto}}#footer-links span{padding:1rem;text-align:center}#footer-links span h4{margin-top:0}#footer-links span ul{list-style:none;padding:0}#footer-links span ul li{padding:0.25rem 0}#footer-links span ul li a{color:white;opacity:0.5;transition:opacity 250ms ease}#footer-links span ul li a:hover{opacity:1}

#sponsors img{max-height:300px}@media (max-width: 720px){#sponsors img{max-width:300px}}.hash_link_white{text-decoration:none;color:white}

#contact-form{display:flex;flex-flow:column;align-items:center}#contact-form input,#contact-form textarea,#contact-form button{display:block;padding:0.75rem 1.1rem;width:100%;box-sizing:border-box;max-width:400px;border-radius:0.9rem;font-size:1.1rem;font-family:inherit;border:0;margin-bottom:1rem}#contact-form input::-webkit-input-placeholder, #contact-form textarea::-webkit-input-placeholder, #contact-form button::-webkit-input-placeholder{color:rgba(0,0,0,0.5)}#contact-form input:-ms-input-placeholder, #contact-form textarea:-ms-input-placeholder, #contact-form button:-ms-input-placeholder{color:rgba(0,0,0,0.5)}#contact-form input::-ms-input-placeholder, #contact-form textarea::-ms-input-placeholder, #contact-form button::-ms-input-placeholder{color:rgba(0,0,0,0.5)}#contact-form input::placeholder,#contact-form textarea::placeholder,#contact-form button::placeholder{color:rgba(0,0,0,0.5)}#contact-form input:focus::-webkit-input-placeholder, #contact-form textarea:focus::-webkit-input-placeholder, #contact-form button:focus::-webkit-input-placeholder{color:rgba(0,0,0,0.9)}#contact-form input:focus:-ms-input-placeholder, #contact-form textarea:focus:-ms-input-placeholder, #contact-form button:focus:-ms-input-placeholder{color:rgba(0,0,0,0.9)}#contact-form input:focus::-ms-input-placeholder, #contact-form textarea:focus::-ms-input-placeholder, #contact-form button:focus::-ms-input-placeholder{color:rgba(0,0,0,0.9)}#contact-form input:focus::placeholder,#contact-form textarea:focus::placeholder,#contact-form button:focus::placeholder{color:rgba(0,0,0,0.9)}#contact-form textarea{resize:none}#contact-form button{background-color:#e14e54;transition:background-color 250ms ease, color 250ms ease}#contact-form button:disabled{color:rgba(255,255,255,0.75);border:1px solid rgba(255,255,255,0.75)}#contact-form button:enabled{border:1px solid white;color:white}#contact-form button:enabled:hover{background-color:white;color:#e14e54}#contact-form{text-align:center;padding-top:1.34rem}

#our-sponsors{grid-column:1 / -1}#banner{height:55vh;background-position:center;background-size:cover;display:grid;grid-template-columns:1fr 4fr 1fr;place-items:center}#about-header{margin:0 6vw}.mission{padding:1rem;background-color:rgba(255,255,255,0.7);font-size:2.75rem;text-align:center;grid-column:2}@media (max-width: 720px){.mission{font-size:7.65vw}}

.iframe-parent{max-width:640px;margin:14px 0}.iframe-container{padding:56.25% 0 0;position:relative}iframe{position:absolute;top:0;left:0;width:100%;height:100%;border:0}

.project-card{padding-top:5vh}.spacing{margin-top:5vh}.spacing p{white-space:pre-line}.top-section{padding:2vh 6vw}.logo-container{display:flex;justify-content:center}.logo-img{max-height:300px}.screen-shot-section{padding:2vh 6vw}.screen-shot-image{max-height:200px}

#nav-wrapper{z-index:1;position:-webkit-sticky;position:sticky;top:0}#nav-top{font-size:0.8rem;background-color:white;display:flex;align-items:center;justify-content:space-between;flex-wrap:nowrap;width:100%;border-bottom:0.1rem solid #e14e54}@media (max-width: 720px){#nav-top{flex-wrap:wrap}}#nav-logo{padding:1.375rem 0;height:2.25rem;margin-left:0.8rem;vertical-align:middle}#nav-trigger{display:none}@media (max-width: 720px){#nav-trigger{display:block;cursor:pointer;background-color:#e14e54;color:white;border-radius:0.25rem;padding:0.6rem 0.75rem;margin:0 0.8rem}#nav-trigger svg{margin-right:0.2rem}}.nav-animatable #nav-navigation{transition:-webkit-transform 250ms ease;transition:transform 250ms ease;transition:transform 250ms ease, -webkit-transform 250ms ease}@media (max-width: 720px){#nav-navigation{position:absolute;width:100%;z-index:-1;top:5rem;background-color:white;will-change:transform;-webkit-transform:translateY(-100%);transform:translateY(-100%);border-top:0.1rem solid #e14e54}#nav-navigation.visible{border-bottom:0.1rem solid #e14e54;-webkit-transform:none;transform:none}}#nav-buttons{display:flex;align-items:center}@media (max-width: 720px){#nav-buttons{flex-direction:column;padding:0.35rem 0}}.nav-button{display:inline-block;font-weight:500;color:black;vertical-align:middle;padding:0.35rem 0 0;margin:0 0.8rem}@media (max-width: 720px){.nav-button{text-align:center;padding:0.35rem 0}}.nav-button:after{margin-top:0.25rem;display:block;content:'';border-bottom:0.1rem solid rgba(0,0,0,0.2);-webkit-transform:scaleX(0);transform:scaleX(0);transition:border-color 250ms ease, -webkit-transform 250ms ease;transition:transform 250ms ease, border-color 250ms ease;transition:transform 250ms ease, border-color 250ms ease, -webkit-transform 250ms ease}.nav-button:hover:after{-webkit-transform:scaleX(1);transform:scaleX(1)}.active-nav-button:after,.active-nav-button:hover:after{-webkit-transform:scaleX(1);transform:scaleX(1);border-bottom:0.1rem solid #e14e54}#donate-button{color:#e14e54;border-color:#e14e54;margin:0 0.8rem 0 0;font-weight:600;transition:background-color 250ms ease, color 250ms ease}@media (max-width: 720px){#donate-button{margin:0.35rem 0}}#donate-button:hover{background-color:#e14e54;color:white}

html{font-size:20px}body{margin:0;font-family:'Raleway', sans-serif;background-color:white}a{text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.content{z-index:0}.separator{height:4rem}.text{padding:4vh 6vw}.split-image{display:flex}.split-image>*{width:50%}@media (max-width: 1205px){.split-image>*{font-size:1.7vw}}@media (max-width: 720px){.split-image>*{font-size:1rem;width:100%}}.inverted{background-color:#1b1c1d;color:white}.secondary{background-color:#e14e54;color:white}.secondary-text{-webkit-text-decoration-color:#e14e54;text-decoration-color:#e14e54;color:#e14e54}.pill-button,.pill-button:hover{display:inline-flex;align-items:center;padding:0.25rem 0.75rem;border:0.05rem solid;border-radius:8rem}.pill-button.secondary,.pill-button:hover.secondary{border-color:#e14e54}.pill-button.inverted,.pill-button:hover.inverted{border-color:black;background-color:black;color:white}.row{display:grid;grid-template-columns:repeat(3, 1fr);place-items:center;grid-column-gap:2rem;grid-row-gap:2rem}@media (max-width: 720px){.row{grid-template-columns:1fr}}.row>*{text-align:center}.row img{max-width:100%}.two-col-grid{display:grid;grid-template-columns:repeat(2, auto);align-items:start;grid-row-gap:2rem;grid-column-gap:2rem}@media (max-width: 720px){.two-col-grid{grid-template-columns:auto}}.social-links a{margin-right:0.5rem}.social-links a svg{font-size:1.5rem}.page{padding:0 6vw 4vh}@media (max-width: 720px){.desktop-only{display:none}}a{color:black}#us,#featured,#organization,#sponsors,#contact,#culture,#how,#join,#faq{margin-top:-5rem;padding-top:5rem}

